<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="coupon">
        <ToolBar>
            <el-button type="primary" icon="el-icon-s-promotion" size="small" @click="sendCoupon">发放优惠券</el-button>
            <div>

                <el-select v-model="searchParams.postType" size="small" clearable placeholder="优惠券名称"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="状态"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-button type="success"  size="small" @click="">查询</el-button>
                <el-button type="warning"  size="small" @click="">重置</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="优惠券编号"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="优惠券名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="优惠券面值">
            </el-table-column>
            <el-table-column
                prop="address"
                label="用户手机号">
            </el-table-column>
            <el-table-column
                prop="address"
                label="所在城市">
            </el-table-column>
            <el-table-column
                prop="address"
                label="优惠券有效期">
            </el-table-column>
            <el-table-column
                prop="address"
                label="获取路径">
            </el-table-column>
            <el-table-column
                prop="address"
                label="使用状态">
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import SendCoupon from './SendCoupon.vue'
    export default {
        mixins:[paginationMixin],
        data() {
            return {
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Loremlique unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dcere laudique unde.'
                }]
            }
        },
        methods: {
            sendCoupon(){
                const h = this.$createElement;
                this.$msgbox({
                    title: '发放优惠券',
                    message: h('SendCoupon', {props:{}},),
                    showCancelButton: true,
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    beforeClose: (action, instance, done) => {
                        if (action === 'confirm') {
                            console.log(instance.name)
                            instance.confirmButtonLoading = true;
                            instance.confirmButtonText = '执行中...';
                            setTimeout(() => {
                                done();
                                setTimeout(() => {
                                    instance.confirmButtonLoading = false;
                                }, 300);
                            }, 1000);
                        } else {
                            done();
                        }
                    }
                }).then(action => {
                    this.$message({
                        type: 'info',
                        message: '发放优惠券成功'
                    });
                }).catch(_=>{})
            },
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {}
                });
            },
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination ,SendCoupon
        }
    }
</script>
